.list2 {
    margin: 8px auto;
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    position: relative;
    overflow: hidden;
    position: relative;
  }
  
  .list2:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
  }
  
  .timeline2 {
    position: absolute;
    top: 0;
    left: 10%;
    width: 2px;
    height: 100%;
    background-color: #999;
  }
  
  .timeline-item2 {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 12px;
    height: 12px;
    background-color: #333;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
  }
  
  .position2 {
    font-weight: 500;
    font-size: 1.8rem;
    margin-bottom: 8px;
    color: #333; /* Renk eklendi */
  }
  
  .position2 a {
    font-weight: 600;
    text-decoration: none;
    color:  #3498db; /* Link rengi eklendi */
  }
  
  .position2 span {
    font-weight: 700;
    color: black
  }
  
  .ta2 {
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 8px;
  }

  
  .ta2 span {
    color:  #3498db /* Link rengi eklendi */

  }
  
  .work2 {
    font-size: 1.2rem;
    width: 100%;
    font-weight: 400;
    margin-bottom: 16px;
  }
  
  .experience2 {
    margin-top: 16px;
    margin-bottom: 16px;
  }
  
  .title2 {
    font-weight: 800;
    font-size: 2.5rem;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;
    text-align: center;
    color: #333;

  }
  
  .details2 {
    width: 50%;
    margin: 0 auto;
    position: relative;
  }
  
  .details2 ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    margin-left: 20px; /* Kutucukların sol tarafındaki mesafe */
  }
  

  
  @media screen and (max-width: 768px) {
    .list2 {
        width: 100%; /* veya istediğin başka bir değer */
    }

    .details2 {
        width: 100%; /* veya istediğin başka bir değer */
        margin: 0 auto;
    }
}