/* Portfolio.module.css */

.portfolio {
    padding: 2.5rem 0;
}

.title h2 {
    text-align: center;
    font-size: 2.5rem;
    color: #333; /* Başlık rengi eklendi */
}

.cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin: 4rem 5vw;
    padding: 0;
    list-style: none;
}

.cards li {
    width: 100%;
    overflow: hidden; /* Kart içeriğini gizleme */
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
}

.cards li:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2), 0 16px 32px rgba(0, 0, 0, 0.2);
}

.cards img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.cards .card-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: 'Poppins', sans-serif;
    background-color: rgba(255, 255, 255, 0.9);
    color: #333;
    z-index: 1;
    transform: translateY(100%);
    transition: 0.3s ease-in-out;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.cards li:hover .card-overlay {
    transform: translateY(0);
}

.cards .card-header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 1.5em;
    background-color: rgba(255, 255, 255, 0.9);
    transform: translateY(0);
    transition: 0.3s ease-in-out;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.cards li:hover .card-header {
    transform: translateY(0);
}

.cards .card-content a {
    background: linear-gradient(45deg, #3498db, #6f7bff);
    color: #fff;
    font-weight: 600;
    padding: 10px 16px;
    border-radius: 10px;
    letter-spacing: 1px;
    font-size: 1rem;
    transition: 0.5s;
    text-decoration: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.cards .card-content a:hover {
    background: linear-gradient(45deg, #2980b9, #6f7bff);
    letter-spacing: 2px;
}

.cards .card-title {
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 12px;
    z-index: 2;
    color: #333;
}

.cards .card-description {
    font-size: 1rem;
    padding: 0 2rem 2rem;
    margin: 0;
    overflow: hidden;
    color: #555;
}


/* Responsive stiller */

/* Küçük ekranlar (Mobil) */
@media screen and (max-width: 480px) {
    .cards {
        grid-template-columns: 1fr;
        margin: 2rem 5vw;
    }
}

/* Orta boy ekranlar (Tablet) */
@media screen and (min-width: 481px) and (max-width: 768px) {
    .cards {
        grid-template-columns: repeat(2, 1fr);
        margin: 3rem 5vw;
    }
}

/* Büyük ekranlar */
@media screen and (min-width: 769px) and (max-width: 1200px) {
    .cards {
        grid-template-columns: repeat(3, 1fr);
        margin: 4rem 5vw;
    }
}
