/* Cards.module.css */

.card {
    position: relative;
    display: block;
    height: 480px;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
}

.card img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.card-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-family: 'Poppins', sans-serif;
    background-color: rgba(255, 255, 255, 0.9);
    color: #333;
    z-index: 1;
    transform: translateY(100%);
    transition: 0.3s ease-in-out;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.card:hover .card-overlay {
    transform: translateY(0);
}

.card-header {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 1.5em;
    background-color: rgba(255, 255, 255, 0.9);
    transform: translateY(0);
    transition: 0.3s ease-in-out;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.card:hover .card-header {
    transform: translateY(0);
}

.card-content a {
    background: linear-gradient(45deg, #3498db, #6f7bff);
    color: #fff;
    font-weight: 600;
    padding: 10px 16px;
    border-radius: 10px;
    letter-spacing: 1px;
    font-size: 1rem;
    transition: 0.5s;
    text-decoration: none;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.1);
}

.card-content a:hover {
    background: linear-gradient(45deg, #2980b9, #6f7bff);
    letter-spacing: 2px;
}

.card-title {
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 12px;
    z-index: 2; /* Kart başlığının içeriği üzerinde kalmasını sağlar */
    color: #333;
}

.card-description {
    font-size: 1rem;
    padding: 0 2rem 2rem;
    margin: 0;
    overflow: hidden;
    color: #555;
}



/* Küçük Ekranlar (Mobil) */
@media screen and (max-width: 767px) {
    .card {
        border-radius: 12px;
    }

    .card-header {
        padding: 1.5em;
    }

    .card-title {
        font-size: 1.1rem;
        margin-bottom: 8px;
    }

    .card-description {
        font-size: 0.9rem;
        padding: 0 1rem 1rem;
    }

    .card-content a {
        padding: 6px 10px;
        font-size: 0.9rem;
    }
}
